:root {
  --color-primary: #ffa07a;
  --color-white: #eef1ef;
  --color-black: #111;
  --color-grey-100: #2b2b2b;
  --color-grey-200: #242424;
  --color-pink-100: #dcc6e0;
}

body {
  background-color: var(--color-black);
  color: var(--color-white);
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, sans-serif;
  font-size: 14px;
  line-height: 1.5;
}

* {
  box-sizing: border-box;
}

button {
  background-color: var(--color-primary);
  border: 0;
  border: 2px solid var(--color-primary);
  color: var(--color-grey-100);
  cursor: pointer;
  border-radius: 6px;
  padding: 4px 8px;
  font-size: 14px;
}

button.cta {
  padding: 6px 14px;
  transition: border-radius .2s ease-in-out;
}

button.cta:hover {
  border-radius: 8px;
}

.logo {
  text-align: center;
  margin-top: 128px;
  margin-bottom: 12px;
  font-size: 38px;
  font-weight: 900;
}

@media (width <= 700px) {
  .logo {
    margin-top: 40px;
  }
}

.logo span {
  color: var(--color-grey-100);
  font-weight: normal;
}

.description {
  text-align: center;
  max-width: 450px;
  margin: 0 auto 100px;
  padding: 0 18px;
  font-size: 14px;
}

@media (width <= 700px) {
  .description {
    margin-bottom: 40px;
  }
}

.columns {
  max-width: 820px;
  margin: 0 auto 32px;
  display: flex;
}

@media (width <= 700px) {
  .columns {
    flex-direction: column-reverse;
  }
}

.columns > * {
  flex: 1;
  width: 50%;
}

@media (width <= 700px) {
  .columns > * {
    width: 100%;
  }
}

.columns pre {
  margin: 0;
}

#counter {
  justify-content: center;
  align-items: center;
  width: 60%;
  display: flex;
}

@media (width <= 700px) {
  #counter {
    width: 100%;
  }
}

#counter button {
  cursor: pointer;
  color: var(--color-primary);
  background-color: var(--color-grey-200);
  border: 1px solid var(--color-primary);
  will-change: transform, box-shadow;
  justify-content: center;
  align-items: center;
  width: 26px;
  height: 26px;
  font-size: 16px;
  font-weight: bold;
  transition: transform .3s ease-in-out, box-shadow .3s ease-in-out;
  display: flex;
}

#counter button:hover {
  transform: scale(1.05);
  box-shadow: 0 0 10px #000000bf;
}

#counter button + div {
  padding: 18px 0 20px;
  font-size: 72px;
  font-weight: 900;
  line-height: 1;
}

@media (width <= 700px) {
  #counter button + div {
    margin: 0 24px;
  }
}

#counter > div {
  flex-direction: column;
  align-items: center;
  display: flex;
}

@media (width <= 700px) {
  #counter > div {
    flex-direction: row-reverse;
  }
}

.content {
  width: 500px;
  margin-left: 230px;
  padding: 18px;
}

code {
  border-radius: 6px;
  padding: 2em !important;
}

@media (width <= 700px) {
  code {
    border-radius: 0;
  }
}

.hljs {
  background-color: var(--color-grey-200);
}

.recipe {
  max-width: 450px;
  margin: 0 auto;
  padding-top: 40px;
}

.recipe a {
  color: #fff;
}

.recipe h2 {
  margin-top: 0;
}

